<template>
  <div class="box-search-mobile" :class="{'disableBoxSearchMobile':!getIsMobile}">test</div>
</template>
<script>
import { mapGetters } from "vuex";
export default {
  computed: {
    ...mapGetters("menuheader", ["getIsSearching", "getIsMobile"])
  }
};
</script>
<style lang="scss" scoped>
.box-search-mobile {
  position: absolute;
  top: 0;
}
.disableBoxSearchMobile {
  display: none;
}
</style>





